export default {
  name: 'ScenicListType',
  props: {
    tags: Array
  },
  data () {
    return {
      tagsList: null
    }
  },
  watch: {
    tags (newVal, oldVal) {
      if (newVal) {
        this.tagsList = this.tags
        this.tagsList.forEach((element, index) => {
          this.$set(element, 'selected', index === 0)
        })
      }
    }
  },
  methods: {
    itemClick (item) {
      this.tagsList.forEach(element => {
        element.selected = element === item
      })
      this.$emit('tagsClick', item)
    }
  }
}
